import React from "react";
import styled from "styled-components";

const Item =({id, name, cost, value, purchasedItems, handleClick})=> {
    // console.log(purchasedItems, id, purchasedItems[id]);
    return(
        <ItemDiv>
            <SubDiv>
                <Name>{name}</Name>
                <Description>Cost: {cost} cookie(s). Produces value: {value} cookies/second.</Description>
            </SubDiv>
            <SubDiv>
                <Count>{purchasedItems[id]}</Count>
            </SubDiv> 
        </ItemDiv>
    );
};

const ItemDiv = styled.div`
color:white; 
text-align: left;
display:flex;
justify-content: space-between;
border-bottom: 1px solid grey; 
padding: 5px;
`

const SubDiv = styled.div`
margin: 5px;
flex-direction:column;
align-self: center;
`

const Name = styled.h4`
font-size: 32px;
`

const Description = styled.p`
font-size: 24px;
color:grey;
`

const Count = styled.p`
font-size: 52px;
margin-left: 30px;
`

export default Item;